
'use server';

import type { Driver } from "@/lib/truckershub-types";

type ApiResponse = {
    status?: boolean;
    response?: {
        drivers: Driver[]
    };
    drivers?: Driver[];
};

async function fetchFromTruckersHub<T>(endpoint: string): Promise<T | null> {
    const apiKey = process.env.TRUCKERSHUB_API_KEY;
    if (!apiKey) {
        console.error("TRUCKERSHUB_API_KEY is not set.");
        return null;
    }

    try {
        const url = new URL(`https://api.truckershub.in/v1/${endpoint}`);
        const res = await fetch(url.toString(), {
             headers: {
                'Authorization': apiKey,
                'Content-Type': 'application/json',
                'User-Agent': 'TamilPasangaVTC/1.0',
            },
            next: { revalidate: 300 } // Revalidate every 5 minutes
        });

        if (!res.ok) {
            console.error(`Failed to fetch from TruckersHub (${endpoint}):`, res.status, await res.text());
            return null;
        }
        
        const data: ApiResponse = await res.json();

        // Handle both response structures from the API
        if (data && (data.status || data.drivers)) {
            return (data.response || data) as T;
        }

        if (data && data.status === false) {
             // Avoid logging empty responses as errors
             if (Object.keys(data).length > 2) { // i.e., more than just status and response
                console.error(`TruckersHub API returned status false for ${endpoint}:`, data);
             }
             return null;
        }
        
        // Log unexpected structures but don't fail if it's just an empty object for a valid response
        if (Object.keys(data).length > 0) {
            // console.warn(`Potentially invalid API response structure from TruckersHub (${endpoint}):`, data);
        }

        return { drivers: [] } as T; // Return empty array for drivers endpoint if response is empty

    } catch (error) {
        console.error(`Error fetching from TruckersHub (${endpoint}):`, error);
        return null;
    }
}


export async function getDrivers(): Promise<Driver[]> {
    const data = await fetchFromTruckersHub<{drivers: Driver[]}>('drivers');
    return data?.drivers || [];
}
